






























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:"",
			swipeList: wx.getStorageSync('banderbuild')||[],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			newlist: [],
			recomlist: [],
			classlist:[],
			provinceName:'',
			countyAreaName:'',
			cityName:'通州'
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	onShow() {
		const reload = ut.checkPageTime('index_build_time');
		console.log(reload)
		if(!this.newlist.length||reload){
			this.req_new();
		}
		if(!this.recomlist.length||reload){
			this.req_recom();
		}
		if(!this.classlist.length||reload){
			this.req_class();
		}
		if(!this.swipeList.length||reload){
			this.req_banner();
		}
	},
	methods: {
		bindRegionChange: function (e) {
			this.provinceName=e.target.value[0];
			this.countyAreaName=e.target.value[1];
			this.cityName=e.target.value[2];
		},
		call(){
			ut.call();
		},
		go_build_build2(_id){
			wx.navigateTo({
				url: `../build/build2?_id=${_id}`
			})
		},
		go_build_serch(){
			wx.navigateTo({
				url: '../build/serch'
			})
		},
		go_build_mallinf(_id,name){
			wx.navigateTo({
				url: `../build/mallinf?_id=${_id}&&title=${name}`
			})
		},
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/class"
			}).then(data=>{
				console.log(data)
				this.classlist=data;
			})
		},
		req_new(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/newlist"
			}).then(data=>{
				this.newlist=data
			})
		},
		req_recom(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/recommendlist"
			}).then(data=>{
				this.recomlist=data
			})
		},
		req_banner(){
			ut.request({
				allurl: ut.uploadimgurl+"common/banner",
				data:{type:2},
				method:'GET'
			}).then(data=>{
				this.swipeList=data;
				wx.setStorageSync('banderbuild',this.swipeList);
			})
		}
	}
}
